const html = require('choo/html')
const css = 0
const { DateTime, Duration } = require('luxon')
const { compose, invoke, map, nth, toPairs } = require('./helpers')
const intervals = require('./intervals')

const prefix = ((require('sheetify/insert')("._e7f5d056 { }\n  ._e7f5d056 .units {\n    color: #CCC;\n    display: flex;\n    flex-wrap: wrap;\n  }\n  ._e7f5d056 .unit {\n    border: 1px solid #ccc;\n    padding: 0px 5px;\n    border-right: 0;\n    border-bottom: 0;\n    margin-top: 5px;\n    margin-right: 5px;\n    margin-bottom: 1px;\n  }\n  ._e7f5d056 .unit.on {\n    border-color: #DF928E;\n    color: #DF928E;\n  }\n\n  ._e7f5d056 .week { }\n  ._e7f5d056 .day { }\n  ._e7f5d056 .hour { }\n  ._e7f5d056 .minute { }\n\n  ._e7f5d056 > .weeks { }\n  ._e7f5d056 > .weeks .week { }\n  ._e7f5d056 > .weeks .day { }\n  ._e7f5d056 > .weeks .hour { }\n  ._e7f5d056 > .weeks .minute { }\n\n  ._e7f5d056 > .days { }\n  ._e7f5d056 > .days .day { }\n  ._e7f5d056 > .days .hour { }\n  ._e7f5d056 > .days .minute { }\n\n  ._e7f5d056 > .hours { }\n  ._e7f5d056 > .hours .hour { }\n  ._e7f5d056 > .hours .minute { }\n\n  ._e7f5d056 > .minutes { }\n  ._e7f5d056 > .minutes .minute { }") || true) && "_e7f5d056")

const visualize = state => {
  const now = DateTime.local()
  const dur = Duration.fromObject({ seconds: state.repeats * state.interval })
  const end = now.plus(dur)

  const denominator = compose(
    map(invoke('replace', /s$/, '')),
    nth(0),
    toPairs,
    invoke('toObject'))(end.diff(now, ['minutes', 'hours', 'days', 'weeks']))

  return html`
    <div>
      <h2>${compose(
        invoke('join'),
        map(([type, count]) => ` ${Math.ceil(count)} ${type}`),
        toPairs)(now.plus(Duration.fromObject({ seconds: state.interval })).diff(now, ['seconds', 'minutes', 'hours', 'days', 'weeks']).toObject())}</h2>
      <section class="${prefix}">
        ${intervals(denominator[0])(Math.ceil(denominator[1]), 0, state.interval, state.depth - 1)}
      </section>
    </div>
    `
}

module.exports.visualize = visualize
